t;caption-side:bottom}th{text-align:inherit}label{display:inline-block;margin-bottom:.5rem}button{border-radius:0}button:focus{outline:1px dotted;outline:5px auto -webkit-focus-ring-color}button,input,optgroup,select,textarea{margin:0;font-family:inherit;font-size:inherit;line-height:inherit}button,input{overflow:visible}button,select{text-transform:none}select{word-wrap:normal}[type=button],[type=reset],[type=submit],button{-webkit-appearance:button}[type=button]:not(:disabled),[type=reset]:not(:disabled),[type=submit]:not(:disabled),button:not(:disabled){cursor:pointer}[type=button]::-moz-focus-inner,[type=reset]::-moz-focus-inner,[type=submit]::-moz-focus-inner,button::-moz-focus-inner{padding:0;border-style:none}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0}input[type=date],input[type=datetime-local],input[type=month],input[type=time]{-webkit-appearance:listbox}textarea{overflow:auto;resize:vertical}fieldset{min-width:0;padding:0;margin:0;border:0}legend{display:block;width:100%;max-width:100%;padding:0;margin-bottom:.5rem;font-size:1.5rem;line-height:inherit;color:inherit;white-space:normal}progress{vertical-align:baseline}[type=number]::-webkit-inner-spin-button,[type=number]::-webkit-outer-spin-button{height:auto}[type=search]{outline-offset:-2px;-webkit-appearance:none}[type=search]::-webkit-search-decoration{-webkit-appearance:none}::-webkit-file-upload-button{font:inherit;-webkit-appearance:button}output{display:inline-block}summary{display:list-item;cursor:pointer}template{display:none}[hidden]{display:none!important}\r\n","@import 'libs/bootstrap-reboot.min';\r\n@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap');\r\n\r\n@import 'base/variables';\r\n@import 'base/mixins';\r\n\r\n\r\n* {\r\n    font-family: 'Poppins', sans-serif;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n.container {\r\n    width: 1140px;\r\n    margin: 0 auto;\r\n}\r\n\r\n.divider {    \r\n    width: 100px;\r\n    height: 3px;\r\n    background-color: $main-color;\r\n    margin: 25px auto 0 auto;  \r\n}\r\n\r\n@import 'blocks/promo';\r\n@import 'blocks/title';\r\n@import 'blocks/_buttons';\r\n@import 'blocks/sidepanel';\r\n@import 'blocks/menu';\r\n@import 'blocks/about';\r\n@import 'blocks/resume';\r\n@import 'blocks/skills';\r\n@import 'blocks/portfolio';\r\n@import 'blocks/price';\r\n@import 'blocks/contacts';\r\n@import 'blocks/pageup';\r\n@import 'blocks/media';\r\n@import 'policy';","$main-color: #e7bb6b;\r\n$text-color: #000;\r\n$line-color: #E8E4DE;\r\n//$main-color: #FFA501;",".promo {\r\n    min-height: 100vh;\r\n    position: relative;  \r\n    background: url('../img/main_bg.jpg') no-repeat 50%/cover;\r\n    padding-top: 205px;\r\n\r\n    &__title {\r\n        width: 270px;\r\n        margin-top: 10px;            \r\n    }\r\n    &__subtitle {\r\n        position: relative;\r\n        padding-left: 10px;         \r\n        width: 270px;      \r\n        @include before_circle_revised;            \r\n    }\r\n    &__btns {\r\n        margin-top: 25px;\r\n        display: flex;\r\n        width: 250px;\r\n        align-items: center;\r\n        justify-content: space-between;\r\n        padding-bottom: 10px;\r\n        .btn {\r\n            &:hover {\r\n            transform: scale(1.05);\r\n            filter: saturate(80%);;\r\n            } \r\n        }\r\n    }\r\n    &__link {\r\n        color: #000000;\r\n        font-weight: bold;\r\n\r\n        &:hover{\r\n            color: #000000;\r\n            text-decoration: none;\r\n            transform: scale(1.05);\r\n        }\r\n\r\n    }   \r\n\r\n}\r\n","@mixin before_circle {\r\n    &:before {\r\n        content: '';\r\n        position: absolute;        \r\n        z-index: -1;\r\n        top: 0px;\r\n        //left: 2;\r\n        right:5px;\r\n        width: 45px;\r\n        height: 45px;\r\n        border-radius:100%;\r\n        background: $main-color;\r\n\r\n    }\r\n\r\n}\r\n@mixin before_circle_revised {\r\n    &:before {\r\n        content: '';\r\n        position: absolute;        \r\n        z-index: -1;\r\n        top: 0px;\r\n        left: 13;        \r\n        width: 40px;\r\n        height: 40px;\r\n        border-radius:100%;\r\n        background: $main-color;\r\n\r\n    }\r\n\r\n}\r\n@mixin skills_circle {\r\n    &:before {\r\n        content: '';\r\n        position: absolute;        \r\n        z-index: -1;\r\n        top: -5px;\r\n        left: -5px;        \r\n        width: 40px;\r\n        height: 40px;\r\n        border-radius:100%;\r\n        background: $main-color;\r\n\r\n    }\r\n\r\n}",".title {\r\n    font-weight: bold; \r\n    color: #2d2a26;\r\n    font-size: 36px; \r\n    \r\n    &__fs14 {\r\n        font-size: 14px;\r\n        line-height: 21px;\r\n    }\r\n\r\n    &__fs16 {            \r\n        font-size: 16px;\r\n        line-height: 24px;\r\n    }\r\n    &__fs20 {            \r\n        font-size: 20px;\r\n        line-height: 30px;              \r\n    }\r\n    &__fs36 {            \r\n        font-size: 36px;  \r\n        line-height: 45px;   \r\n        font-weight: normal;          \r\n    }\r\n\r\n    &__fs48 {\r\n        font-size: 48px;                \r\n    }\r\n    &__section-title {\r\n        position: relative;\r\n        text-align: center;\r\n        padding: 2px 0 0 13px;\r\n        @include skills_circle;\r\n        &:before {\r\n            top: -6px;\r\n            left: 50%;\r\n            transform: translateX(-50%);\r\n        }\r\n    }\r\n    &__section-subtitle {\r\n        text-align: center;\r\n    }  \r\n}\r\n\r\n.frame {\r\n    opacity:75%;\r\n    background-color: #ece6de;\r\n    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.65);\r\n    border-radius: 3px;\r\n    padding: 5px;\r\n    text-align: center;        \r\n}",".btn {    \r\n    height: 45px;\r\n    background: $main-color;\r\n    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.65);\r\n    text-align: center;\r\n    border-radius: 2px;\r\n    padding: 12px 25px;\r\n    &:hover {\r\n        transform: scale(1.05);\r\n        filter: saturate(80%);;\r\n        } \r\n\r\n}",".sidepanel {\r\n    position: fixed;\r\n    top: 100px;\r\n    left: 50px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    height: 340px;\r\n    z-index: 3;    \r\n        &__divider {\r\n            width: 2px;\r\n            height: 70px;\r\n            background-color: $text-color;\r\n        }\r\n        &__text {\r\n            height: 150px;\r\n            width: 25px;\r\n            color:rgb(30, 159, 159);\r\n            span {\r\n                transform: rotate(-90deg) translate(-30%,-260%);\r\n                display: block;\r\n                width: 150px;\r\n                color: $text-color;\r\n                font-weight: bold;\r\n                opacity:75%;\r\n                background-color: #ece6de;\r\n                box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.65);\r\n                border-radius: 3px;\r\n                text-align: center;\r\n                \r\n            }\r\n        }\r\n        &__link{\r\n            color:$main-color;  \r\n            transform: rotate(-90deg); \r\n            img {\r\n                width: 30px;\r\n                height:30px;    \r\n                opacity:75%;\r\n                background-color: #ece6de;\r\n                border-radius: 15px;\r\n                &:hover {\r\n                    transform: scale(1.20);                    \r\n                    } \r\n            }     \r\n        }\r\n}\r\n",".hamburger {\r\n    position: fixed; // нужно, чтобы блок \"висел\" во всех секциях\r\n    top: 25px;\r\n    right: 30px;\r\n    height: 30px;\r\n    padding: 5px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n    align-items: flex-end;\r\n    cursor: pointer;\r\n    background-color: #ece6de;\r\n    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.65);\r\n        &